import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, ParamMap} from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import 'rxjs/add/operator/switchMap';
import { UserService } from '../user.service';
import { User } from '../user';

@Component({
  selector: 'app-edituser',
  templateUrl: './edituser.component.html',
  styleUrls: ['./edituser.component.css']
})
export class EdituserComponent implements OnInit {
  editForm: FormGroup;
  editUser: User;
  updating: boolean;
  updated: boolean;
  serverError: String;
  constructor(
    private userService: UserService,
    private route: ActivatedRoute,
    private router: Router,
    private formbuilder: FormBuilder) {
      this.createForm();
      this.updated = false;
    }

  ngOnInit() {
    // this.editUser = new User();
    // this.editUser.company="Hard";
    // this.editUser.username = "Shijin";
    // this.editUser.id=100;
    // this.editUser.roles = "ROLE_USER, ROLE_ADMIN";
    // this.setCheckbox();
    // this.fillInForm();

    this.route.paramMap.switchMap(
      (params: ParamMap) => this.userService.getUser(+params.get('id'))
    ).subscribe(user1 => {
      this.editUser = user1;
      console.log(user1);
      console.log(user1.username);
      this.fillInForm();
      });

    }

    createForm() {
      this.editForm = this.formbuilder.group({
        id: '',
        company: ['', Validators.required],
        username: ['', Validators.required],
        role_user: [false],
        role_admin: [false]
      });
    }

    fillInForm() {
      var roles = this.editUser.roles;
      if (roles == null) roles = '';

      this.editForm.setValue({
        id: this.editUser.id,
        company: this.editUser.company,
        username: this.editUser.username,
        role_user: roles.search("ROLE_USER") != -1,
        role_admin: roles.search("ROLE_ADMIN") != -1
      });
    }

    fillInUser() {
      this.editUser.company = this.editForm.get("company").value;
      this.editUser.username = this.editForm.get("username").value;
      this.editUser.roles = this.userService.getUserRoles(this.editForm.get("role_user").value, this.editForm.get("role_admin").value);
    }

    updateUser(): void {
      this.updating = true;
      this.updated = false;
      this.fillInUser();

      this.userService.updateUser(this.editUser).then(user => {
        this.editUser = user;
        console.log("user id: " + user.id);
        this.updating = false;
        this.updated = true;
        this.editForm.reset();
        this.serverError = undefined;
        this.fillInForm();

      }, error => {
        console.log("get error message:" + error);
        this.serverError = error;
      });

    }

    gotoUserList(): void {
      let link = ['/userslist'];
      this.router.navigate(link);
    }

}



