import { Injectable } from '@angular/core';
import { Headers, Http, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { User } from './user';
import { UserListQueryParameters } from './users-list/UserListQueryParameters';

@Injectable()
export class UserService {
  private logoutUrl = 'logout';
  private usersListUrl = 'userslist';
  private usersListCountUrl = 'userslistcount';
  private userUrl = 'user';
  private editUserUrl = 'u'

  private headers = new Headers({'Content-Type': 'application/json'});

  constructor(private http: Http) { }

  getUser(id: number): Promise<User> {
    console.log("UserService.getUser()...");
    return this.http.get(`${this.userUrl}/${id}`).toPromise()
            .then(response => response.json() as User).catch(this.handleError);
  }

  uploadImage(id: number, formData: FormData): Promise<string> {
    let headers = new Headers();
        /** No need to include Content-Type in Angular 4 */
        //headers.append('Content-Type', 'multipart/form-data');
    headers.append('Accept', 'application/json');
    let options = new RequestOptions({ headers: headers });
    return this.http.post(`${this.userUrl}/${id}/photo`, formData, options)
      .toPromise()
      .then(response => {
        console.log("upload success:" + response);
        return response.text() as string; })
      .catch(this.handleError);

  }

  addUser(user: User): Promise<User> {
    console.log("UserService.addUser()...")
    return this.http.post(this.userUrl, JSON.stringify(user), {headers: this.headers}).toPromise()
                .then(response => {
                  console.log("response = " + response);
                  return response.json() as User;
                })
                .catch(this.handleError);

  }

  updateUser(user: User): Promise<User> {
    console.log("UserService.addUser()...")
    return this.http.put(`${this.userUrl}/${user.id}`, JSON.stringify(user), {headers: this.headers}).toPromise()
                .then(response => {
                  console.log("response = " + response);
                  return response.json() as User;
                })
                .catch(this.handleError);
  }

  deleteUser(id: number): Promise<string> {
    console.log("UserService.addUser()...");
    return this.http.delete(`${this.userUrl}/${id}`, {headers: this.headers}).toPromise()
                .then(response => {
                  console.log("response = " + response);
                  return response.json() as string;
                })
                .catch(this.handleError);
  }

  getUserListCount(param: UserListQueryParameters) : Promise<number> {
    console.log("UserService.getUserList()..." + param);
    return this.http.post(this.usersListCountUrl, JSON.stringify(param), {headers: this.headers}).toPromise()
                .then(response => {
                  console.log("UserService.getUserListCount() -> response = " + response);
                  return response.json() as number;
                })
                .catch(this.handleError);
  }

  getUserList(param: UserListQueryParameters) : Promise<User[]> {
    console.log("UserService.getUserList()..." + param);
    return this.http.post(this.usersListUrl, JSON.stringify(param), {headers: this.headers}).toPromise()
                .then(response => {
                  console.log("UserService.getUserList() -> response = " + response);
                  return response.json() as User[];
                })
                .catch(this.handleError);
    // return [
    //   {id: 1, name:'shijin', roles: 'User'},
    //   {id: 2, name:'jeri', roles: 'Admin, User'}
    // ];
  }

  logout(): void {
    console.log("logout...");
    this.http.post(this.logoutUrl, null).toPromise()
      .then(response => {
        console.log("response=" + response);
      }).catch(this.handleError);
    window.location.href="http://localhost:5555/uaa/login";
    console.log("logout...end");
  }

  handleError(error: any) : Promise<any> {
    console.error("An error occurred ", error);
    console.error("error message = ", error._body);
    return Promise.reject(error._body);
  }


    getUserRoles(role_user:boolean, role_admin:boolean): string {
      var roles = '';
      if (role_user) {
        roles = 'ROLE_USER';
      }
      if (role_user && role_admin) {
        roles += ', ROLE_ADMIN';
      } else if (role_admin) {
        roles += 'ROLE_ADMIN';
      }
      return roles;
    }

}



