import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router, ParamMap} from '@angular/router';
import { User } from '../user';
import { UserService } from '../user.service';

@Component({
  selector: 'app-userdetail',
  templateUrl: './userdetail.component.html',
  styleUrls: ['./userdetail.component.css']
})
export class UserdetailComponent implements OnInit {
  userForm: FormGroup;
  user: User;
  isDelete: boolean;
  updated: boolean;
  updating: boolean;
  serverError: String;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private fb: FormBuilder,
    private userService: UserService
  ) {
    this.createForm();
    this.isDelete = this.router.url.search("deleteuser") != -1;
  }

  ngOnInit() {

    this.route.paramMap.switchMap(
      (params: ParamMap) => this.userService.getUser(+params.get('id'))
    ).subscribe(user => {
      this.user = user;
      this.fillInForm();
      });
  }

  createForm() {
    this.userForm = this.fb.group({
      id: '',
      company: '',
      username: '',
      role_user: false,
      role_admin: false
    });
  }

  fillInForm() {
    var roles = this.user.roles;
    if (roles == null) roles = '';

    this.userForm.setValue({
      id: this.user.id,
      company: this.user.company,
      username: this.user.username,
      role_user: roles.search("ROLE_USER") != -1,
      role_admin: roles.search("ROLE_ADMIN") != -1
    });
  }

  gotoUserList(): void {
    let link = ['/userslist'];
    this.router.navigate(link);
  }

  deleteUser() {
    this.updating = true;
    this.userService.deleteUser(this.user.id).then(response => {
      console.log("response=" + response);
      this.updating = false;
      this.updated = true;
      this.serverError = undefined;
    }, error => {
      console.log(error);
      this.serverError = error;
    });
  }
}



