import { Component, OnInit, ViewChild } from '@angular/core';
import { User } from '../user';
import { UserService } from '../user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-adduser',
  templateUrl: './adduser.component.html',
  styleUrls: ['./adduser.component.css']
})
export class AdduserComponent implements OnInit {
  newUser: User;
  role_user: boolean;
  role_admin: boolean;
  serverError: String;
  showPhoto: boolean;
  @ViewChild('fileInput') fileInput;

  constructor(
    private userService: UserService,
    private router: Router
    ) {
    this.role_user = false;
    this.role_admin = false;
    this.showPhoto = false;
    this.newUser = new User();
  }

  ngOnInit() {

  }

  addUser(): void {
    this.newUser.roles = this.userService.getUserRoles(this.role_user, this.role_admin);

    // var str = 'company=' + this.newUser.company;
    // str += ', name=' + this.newUser.username;
    // str += ', roles=' + this.newUser.roles;
    // str += ', id=' + this.newUser.id;
    // alert("str=" + str);

    this.userService.addUser(this.newUser).then(user => {
      this.newUser = user;
      console.log("user id: " + user.id);
      this.serverError = undefined;

      let fileBrower = this.fileInput.nativeElement;
      if (fileBrower.files && fileBrower.files[0]) {
        const formData = new FormData();
        formData.append("file", fileBrower.files[0]);
        this.userService.uploadImage(user.id, formData).then(response => {
          console.log(response);
          //after upload image, display it
          this.showPhoto = true;
        }, error=> {
          this.serverError = error;
        })
      }

    }, error => {
      console.log(error);
      this.serverError = error;
    });


  }

  gotoUserList(): void {
    let link = ['/userslist'];
    this.router.navigate(link);
  }
}



