import { Component, OnInit } from '@angular/core';
import { User } from '../user';
import { UserService } from '../user.service';
import { UserListQueryParameters } from './UserListQueryParameters';
import { Pagination } from './Pagination';
import { USERS } from '../mockUsers';
import { Router } from '@angular/router';

@Component({
  selector: 'app-users-list',
  templateUrl: './users-list.component.html',
  styleUrls: ['./users-list.component.css']
})
export class UsersListComponent implements OnInit {
  users: User[];
  param: UserListQueryParameters;
  pagination: Pagination;

  constructor(
    private userService: UserService,
    private router: Router
  ) {


    this.pagination = new Pagination();
    this.pagination.current = 1;
    this.pagination.countPerPage = 10;
    this.pagination.total = 332;
  }

  ngOnInit() {
    console.log("UsersListComponent is init...");
    //this.users = USERS;

    this.param = new UserListQueryParameters();
    this.param.pageParam = new Pagination();
    this.param.pageParam.current = 1;
    this.param.pageParam.countPerPage = 10;
    this.getUserList(this.param);
  }

  getUserList(param: UserListQueryParameters): void {
    console.log("UsersListComponent.getUserList()..." + param);
    this.userService.getUserList(param).then(users => {
      // console.log("users: " + users);
      this.users = users;
      this.pagination.current = param.pageParam.current;
    });

    this.userService.getUserListCount(param).then(count => {
      this.pagination.total = count;
    });
  }

  /**
  * position: -1:Frist, 0:Previous, 1~5: five buttons, 6: Next, 7:Last
  * return 0: hidden, anyNumber: display the value
  */
  getPageNumber(position: number): number {
    var pos;
    switch (position) {
      case 1:
        pos = this.getFirstPageNumber();
        break;
      case 2:
      case 3:
      case 4:
      case 5:
        pos = this.getPageNumber(position - 1) + 1;
        if (pos == 0 || pos > this.pagination.totalPage) {
          pos = -1;
        }
        break;
    }
      // console.log('pos(' + position + ')=' + pos);
      return pos;
  }

  gotoPage(page: number): void {
    console.log("current page = " + page);
    this.param.pageParam.current = page;
    this.getUserList(this.param);
  }

  getFirstPageNumber(): number {
    var totalPage = Math.ceil(this.pagination.total / this.pagination.countPerPage);
    this.pagination.totalPage = totalPage;
    //console.log('totalPage=' + totalPage);
    if (totalPage < 5) {
      return 1;
    } else if (totalPage - this.pagination.current < 3) {
      return totalPage - 4;
    } else if (this.pagination.current < 4) {
      return 1;
    } else {
      return this.pagination.current - 2;
    }
  }

  getCurrentPosition(): number {
    return this.pagination.current - this.getFirstPageNumber() + 1;
  }

  gotoAddUserPage(): void {
    let link = ['/adduser'];
    this.router.navigate(link);
  }
}



